<template>
  <div id="ledgerEdit">
    <Modal
      v-model="edit"
      title="编辑人类活动监管台账信息"
      @on-cancel="reset">
      <Form ref="editForm" :model="EditData" :rules="EditRule" :label-width="160" style="padding:0 10%;">
        <FormItem prop="id" label="ID：" style="display: none">
          <Input v-model.trim="EditData.id"/>
        </FormItem>
        <FormItem prop="name" label="人类活动监管台账名称：">
          <Input v-model.trim="EditData.name" placeholder="请输入人类活动监管台账名称" size="default"/>
        </FormItem>
        <FormItem prop="remark" label="人类活动监管台账说明：">
          <Input v-model.trim="EditData.remark" type="textarea" :rows="4" placeholder="请输入人类活动监管台账说明" size="default"/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="editSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import {
  // 编辑台账
  editAccount1
} from '@/api/remoteSensing'
export default {
  name: 'Taskedit',
  data () {
    return {
      /* 数据表单校验 */
      EditRule: {
        name: [
          { required: true, message: '人类活动监管台账名称必须填写', trigger: 'blur' }
        ]
      },
      /* 编辑框 */
      edit: false,
      /* 编辑数据 */
      EditData: {}
    }
  },
  props: {
    /* 编辑行原数据 */
    data: {}
  },
  watch: {
    data () {
      this.dataHandle()
    }
  },
  mounted () {},
  methods: {
    /* 编辑框默认值数据处理 */
    dataHandle () {
      this.EditData.id = this.data.id
      // this.EditData.name = this.data.name
      this.EditData.remark = this.data.remark
      this.$set(this.EditData, 'name', this.data.name)
    },
    /* 编辑核查任务 */
    editSubmit () {
      this.$refs.editForm.validate(valid => {
        if (valid) {
          editAccount1({
            data: {
              // createBy: 1, // 创建人id暂时写死
              id: this.EditData.id,
              name: this.EditData.name,
              remark: this.EditData.remark
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '编辑人类活动监管台账成功' })
              this.$emit('getTableData')
              this.edit = false
              this.EditData = {}
              this.reset()
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          })
        }
      })
    },
    // 重置表單參數
    reset () {
      this.EditData = {
        id: '',
        name: '',
        remark: ''
      }
      this.EditData = {}
      this.$refs.editForm.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
