<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: calc(100% - 65px)" id="ledgerQuery"></QueryBox>
      <div class="queryBtns">
        <Button type="success" size="small" icon="md-add" class="btn-add" @click.stop="$refs.ledgerAdd.build = true">新建</Button>
      </div>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight"></Table>
        <div class="page_box">
          <Page :total="total" :page-size="pageSize" :current.sync="pageNum" @on-change="pageChange" show-total/>
        </div>
      </div>
    </div>
    <!-- 详情列表 -->
    <div class="extract_box" :class="{detailActive:detailActive}">
      <div class="queryBox" style="text-align: right">
        <Button size="small" type="primary" icon="ios-arrow-back" @click="detailActive=false">返回</Button>
      </div>
      <div style="padding: 0 15px 15px;height: calc(100% - 70px)">
        <Row style="height: 100%">
          <Col span="11" style="height: 100%">
            <div style="border: 1px solid #d1d1d1;height: 100%;margin-right: 10px">
              <ledgerManageMap ref="ledgerManageMap"></ledgerManageMap>
            </div>
          </Col>
          <Col span="13">
            <Table border stripe :data="data2" :columns="columns2" :max-height="tableHeight"></Table>
            <Page :total="total1"  show-total :page-size="pageSize1" :current.sync="pageNum1" style="width: 100%;padding: 15px 0;text-align: right"/>
          </Col>
        </Row>
      </div>
    </div>
    <!-- 新建-->
    <ledgerAdd ref="ledgerAdd" @getTableData="getTableList"></ledgerAdd>
    <!-- 修改-->
    <ledgerEdit ref="ledgerEdit" :data="editDefaultData" @getTableData="getTableList"></ledgerEdit>
  </div>
</template>

<script>
import QueryBox from '@/components/publicQuery'
import ledgerManageMap from './ledgerManage_map'
import ledgerAdd from './ledgerAdd'
import ledgerEdit from './ledgerEdit'
import {
  getAccountList,
  rlhdDetail,
  delectAccount1, // 删除台账
  deletDataFromGroup // 删除台账中的人类活动
} from '@/api/remoteSensing'
import { renderTime, cacuDMS } from '@/utils/common'
export default {
  name: 'extracTable',
  data () {
    return {
      /* 查询框查询条件 */
      queryData: {},
      typeList: [
        {
          value: '',
          label: '请选择'
        },
        {
          value: '1',
          label: 'type1'
        },
        {
          value: '2',
          label: 'type2'
        }
      ],
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /* 表格数据 */
      columns1: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '人类活动监管台账名称',
          align: 'center',
          minWidth: 200,
          key: 'name'
        },
        {
          title: '日期',
          align: 'center',
          minWidth: 200,
          render: (h, params) => {
            return h('span', renderTime(params.row.updateDate === null ? params.row.createDate : params.row.updateDate))
          }
        },
        {
          title: '人类活动监管台账说明',
          align: 'center',
          minWidth: 200,
          key: 'remark'
        },
        {
          title: '人类活动版块数量',
          align: 'center',
          minWidth: 200,
          key: 'sonCount'
        },
        {
          title: '人类活动面积(公顷)',
          align: 'center',
          minWidth: 200,
          key: 'sonArea'
        },
        {
          title: '操作',
          key: 'address',
          width: 250,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'md-eye'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.tzId = params.row.id
                    /* this.addActive = true
                    this.$refs.ledgerManageMap.initCom() */
                    this.showAccountDetail(params.row.id)
                  }
                }
              }, '查看详情'),
              // 修改
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.editDefaultData = params.row
                    this.$refs.ledgerEdit.edit = true
                  }
                }
              }, '编辑'),
              // 删除
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该台账吗？',
                      onOk: () => {
                        this.infoDel(params.row.id)
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      data1: [],
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 加载 */
      loading: false,
      /* 提取信息框状态 */
      addActive: false,
      /* 编辑框默认原数据 */
      editDefaultData: {},
      /* 台账详情面板 */
      detailActive: false,
      /* 人类活动详情列表数据 */
      data2: [],
      /* 人类活动详情列表的列设置 */
      columns2: [
        {
          title: '序号',
          type: 'index',
          align: 'center',
          width: 60
        },
        {
          title: '活动名称',
          align: 'center',
          minWidth: 200,
          key: 'activeName',
          render: (h, params) => {
            return h('span', {
              style: {
                cursor: 'pointer',
                color: 'rgb(45, 140, 240)',
                textDecoration: 'underline'
              },
              on: {
                click: () => {
                  this.dingwei(params.row.geometry)
                }
              }
            }, params.row.activeName)
          }
        },
        {
          title: '活动类型',
          align: 'center',
          minWidth: 150,
          key: 'activeType'
        },
        {
          title: '所属区域类型',
          align: 'center',
          minWidth: 150,
          key: 'region'
        },
        {
          title: '位置',
          align: 'center',
          minWidth: 130,
          key: 'position'
        },
        {
          title: '面积（公顷）',
          align: 'center',
          minWidth: 170,
          key: 'area'
        },
        {
          title: '操作',
          key: 'address',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              // 删除
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该人类活动吗？',
                      onOk: () => {
                        this.infoDels(params.row.groupId, params.row.cd001)
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      /* 人类活动详情列表的分页器参数 */
      pageSize1: 20,
      pageNum1: 10,
      total1: 0,
      tzId: '' // 台账id
    }
  },
  computed: {
    // 查询表单
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'name',
          title: '人类活动监管台账名称'
        }
      ]
    }
  },
  components: {
    QueryBox,
    // 台账信息地图
    ledgerManageMap,
    // 新建弹窗
    ledgerAdd,
    // 编辑弹窗
    ledgerEdit
  },
  created () {
    this.getTableList()
  },
  mounted () {
    // 窗口改变时图表自适应
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      this.getTableList()
    }, false)
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableList()
    },
    /* 获取字段列表 */
    getTableList () {
      this.loading = true
      getAccountList({
        'data': {
          pageSize: this.pageSize,
          pageNum: this.pageNum,
          ...this.queryData
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
          this.loading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* axios: 删除数据 */
    infoDel (e) {
      delectAccount1({
        data: {
          id: [e]
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.getTableList()
          this.$Notice.success({ title: '删除成功' })
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 信息提取地图页面退出 */
    signOut (addActive) {
      this.addActive = addActive
    },
    /* 展开显示人类活动台账的详情 */
    showAccountDetail (id) {
      this.detailActive = true
      this.pageNum1 = 1
      rlhdDetail({
        data: {
          id: id,
          pageSize: this.pageSize1,
          pageNum: this.pageNum1
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.data2 = res.data.data.rows
          this.total1 = res.data.data.total
        } else {
          this.$Notice.error({ title: '获取人类活动台账详情失败', desc: res.data.msg })
        }
      })
    },
    /* axios: 人类活动台账详情单条删除数据 */
    infoDels (p, e) {
      deletDataFromGroup({
        data: {
          groupId: p,
          id: [e]
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '删除成功' })
          this.showAccountDetail(this.tzId)
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 地图定位 */
    dingwei (geo) {
      this.$refs.ledgerManageMap.renderBankuai(geo)
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
    .btn-exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
    .btn-download {
      background-color: #1ccf20;
      border-color: #1ccf20;
      &:hover, &:focus {
        background-color: #13bb17;
        border-color: #13bb17;
      }
    }
    .btn-tabBtn {
      background: rgb(16,186,204);
      border-color: rgb(16,186,204);
      margin-right: 10px;
      &:hover {
        background: #13B1CC;
        border-color: #13B1CC;
      }
    }
    .iconfont {
      margin-right: 4px;
      &.icon-xuanzewenjian {
        font-size: 12px;
      }
      &.icon-xiazai {
        font-size: 13px;
      }
      &.icon-upload {
        font-size: 12px;
      }
      &.icon-leimupinleifenleileibie {
        font-size: 12px;
      }
    }
    .queryBox {
      height: 50px;
      line-height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .extract_box{
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
    .detailActive{
      right: 0;
    }
    .btn-import {
      background-color: rgb(194,146,1);
      border-color: rgb(194,146,1);
      &:hover, &:focus {
        background-color: #B28301;
        border-color: #B28301;
      }
    }
  }
</style>
