<template>
  <div id="ledgerAdd">
    <Modal
      v-model="build"
      title="新建人类活动监管台账"
      @on-cancel="reset"
    >
      <Form ref="addForm" :model="AddData" :rules="ruleValidate" :label-width="160" style="padding:0 10%;">
        <FormItem prop="name" label="人类活动监管台账名称：">
          <Input v-model.trim="AddData.name" placeholder="请输入人类活动监管台账名称" size="default"/>
        </FormItem>
        <FormItem prop="remark" label="人类活动监管台账说明：">
          <Input v-model.trim="AddData.remark" type="textarea" :rows="4" placeholder="请输入人类活动监管台账说明" size="default"/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="addSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import {
  // 新建活动台账
  createAccount1
} from '@/api/remoteSensing'
export default {
  name: 'TaskAdd',
  data () {
    return {
      /* 添加数据表单参数 */
      AddData: {
        name: '',
        remark: ''
      },
      /* 添加数据表单校验 */
      ruleValidate: {
        name: [
          { required: true, message: '人类活动监管台账名称必须填写', trigger: 'blur' }
        ]
      },
      /* 新建框 */
      build: false
    }
  },
  mounted () {},
  methods: {
    /* 添加核查任务 */
    addSubmit () {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          createAccount1({
            data: {
              createBy: 1, // 创建人id暂时写死
              name: this.AddData.name,
              remark: this.AddData.remark
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '新建人类活动监管台账成功' })
              this.$emit('getTableData')
              this.build = false
              this.AddData = {}
              this.reset()
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          }).catch(() => {
            this.$Notice.error({ title: '服务器错误' })
          })
        }
      })
    },
    // 重置表單參數
    reset () {
      this.AddData = {
        name: '',
        remark: ''
      }
      this.$refs.addForm.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
